let utils = {
    axios: axios
};

/**
 * @method Post
 * @description post
 * @param {String} url - apiurl
 * @param {Object} data - 
 * @param {*} config - axios [https://github.com/mzabriskie/axios]
 */
utils.Post = (url, data = null, config = {}) => {
    return axios({
        method: 'post',
        url: url,
        data: serialize(data),
        withCredentials: true,
        headers: {
            contentType: "application/json"
        },
        ...config
        // adapter: config.mockFlag ? () => MOCK_DATA[config.mock] : null
    })
}

